html {
  scroll-behavior: smooth; }

body {
  margin: 0 auto;
  padding: 22px 24px;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #e5e5e5;
  overflow-x: hidden; }
  @media (max-width: 1279px) {
    body {
      padding: 22px 28px; } }
  @media (max-width: 767px) {
    body {
      padding: 22px 8px; } }

.overflow-hidden {
  overflow: hidden; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .button--success {
    color: #64b851;
    background-color: #f2faf0; }
    .button--success:hover, .button--success:focus {
      background-color: #e1f4dd; }
    .button--success:active {
      background-color: #d1edca; }
  .button--danger {
    color: #eb4b51;
    background-color: #faf0f0; }
    .button--danger svg {
      fill: #eb4b51; }
    .button--danger:hover, .button--danger:focus {
      background-color: #f4dddd; }
    .button--danger:active {
      background-color: #edcaca; }
  .button--primary {
    color: #383736;
    background-color: #fac62d; }
    .button--primary:hover, .button--primary:focus {
      background-color: #f0b505; }
    .button--primary:active {
      background-color: #e1ab08; }

.card {
  position: relative;
  display: flex;
  max-width: 1200px;
  padding: 24px;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: 0 2px 24px rgba(0, 0, 0, 0.84);
  border-radius: 8px; }

.card__button-close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 24px;
  height: 24px; }
  .card__button-close svg {
    fill: #989691; }

.card__description {
  flex-grow: 1; }

.card__commerce {
  width: 308px;
  flex-shrink: 0; }

@media (max-width: 919px) {
  .card {
    flex-direction: column;
    padding: 24px 29px; }
  .card__description {
    margin-bottom: 25px; }
  .card__commerce {
    width: 100%; } }

@media (max-width: 767px) {
  .card {
    padding: 24px 20px; }
  .card__description {
    margin-bottom: 21px; } }

.description {
  display: flex; }

.description__wrapper {
  flex-grow: 1;
  order: 2; }

.description__image {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-width: 178px;
  margin-right: 32px;
  padding: 10px 0 0 0;
  order: 1; }

.description__title {
  margin: 0 0 8px 0;
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  color: #383736; }

.description__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 67.75%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.description__item {
  width: 33.33%;
  margin-bottom: 12px; }
  .description__item dl {
    margin: 0; }
  .description__item dt {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    color: #989691; }
  .description__item dd {
    margin: 0;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    color: #383736; }

.description__info {
  display: flex;
  flex-wrap: wrap; }

.description__info-button {
  flex-shrink: 0;
  min-height: 32px;
  padding: 0 16px 0 14px;
  margin: 6px 16px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  border-radius: 22px; }
  .description__info-button svg {
    margin-right: 10px; }
  .description__info-button:last-child {
    margin-right: 0; }

@media (max-width: 1279px) {
  .description__list {
    width: 82.75%; }
  .description__item {
    width: 42%; } }

@media (max-width: 919px) {
  .description {
    padding-left: 3px; }
  .description__image {
    min-width: 158px;
    margin-right: 42px; }
  .description__list {
    width: 55.75%; } }

@media (max-width: 767px) {
  .description {
    padding-left: 0;
    flex-direction: column; }
  .description__image {
    width: 100%;
